/* Developer.scss */
.tooltip {
  rect {
    fill: #f0f0f0; /* 회색 배경 */
    stroke: #000000; /* 검은색 테두리 */
    stroke-width: 2; /* 테두리 두께를 더 두껍게 */
    rx: 8; /* 모서리를 둥글게 */
    filter: drop-shadow(0 2px 4px rgba(0,0,0,0.3)); /* 그림자 효과, 조금 더 진하게 조정 */
  }

  text {
    fill: #000000; /* 검은색 텍스트 */
    font-size: 12px; /* 글꼴 크기 */
    pointer-events: none; /* 텍스트가 마우스 이벤트 방해 안함 */
  }
}


